.canvas-wrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
  position: relative;
  z-index: 10;
}

.drawing-canvas {
  width: 100%;
  max-width: 800px;
  height: 600px;
  background-color: white;
  border: 2px solid #e0e0e0;
  border-radius: 8px;
  cursor: crosshair;
  touch-action: none; /* Important for touch devices */
  position: relative;
  z-index: 5;
}

.clear-button {
  padding: 0.5rem 1rem;
  background-color: #ff4d4d;
  color: white;
  border-radius: 6px;
  font-weight: 500;
  transition: background-color 0.2s ease;
  z-index: 10;
  position: relative;
}

.clear-button:hover {
  background-color: #e63939;
}

/* Make sure no elements are overlaying the canvas that could block clicks */
.word-selection, .word-display, .waiting-message {
  position: relative;
  z-index: 10;
  background-color: rgba(255, 255, 255, 0.9);
  padding: 1rem;
  margin-bottom: 1rem;
  border-radius: 8px;
  border: 1px solid #e0e0e0;
}